/* English Module Styles */

.english-activity-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.english-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 24px;
  transition: all 0.3s ease;
}

.english-card:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

.letter-tile {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  font-size: 24px;
  font-weight: bold;
  border-radius: 12px;
  margin: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.letter-tile:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.letter-tile.selected {
  background: linear-gradient(135deg, #f093fb, #f5576c);
  border-color: #fff;
  transform: scale(1.1);
}

.letter-tile.correct {
  background: linear-gradient(135deg, #4facfe, #00f2fe);
  animation: pulse 0.6s ease-in-out;
}

.letter-tile.wrong {
  background: linear-gradient(135deg, #fa709a, #fee140);
  animation: shake 0.5s ease-in-out;
}

.word-display {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 20px;
  margin: 20px 0;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.hint-box {
  background: linear-gradient(135deg, #ffecd2, #fcb69f);
  border-radius: 12px;
  padding: 16px;
  margin: 16px 0;
  border: 2px solid #ff8a80;
}

.option-button {
  background: rgba(255, 255, 255, 0.2);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  padding: 16px 24px;
  margin: 8px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.option-button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}

.option-button.selected {
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-color: #fff;
  transform: scale(1.05);
}

.option-button.correct {
  background: linear-gradient(135deg, #4facfe, #00f2fe);
  animation: pulse 0.6s ease-in-out;
}

.option-button.wrong {
  background: linear-gradient(135deg, #fa709a, #fee140);
  animation: shake 0.5s ease-in-out;
}

.part-of-speech-word {
  display: inline-block;
  padding: 8px 16px;
  margin: 4px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.part-of-speech-word.noun {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
}

.part-of-speech-word.verb {
  background: linear-gradient(135deg, #f093fb, #f5576c);
  color: white;
}

.part-of-speech-word.adjective {
  background: linear-gradient(135deg, #4facfe, #00f2fe);
  color: white;
}

.part-of-speech-word.adverb {
  background: linear-gradient(135deg, #fa709a, #fee140);
  color: white;
}

.part-of-speech-word:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.part-of-speech-word.selected {
  border-color: #fff;
  transform: scale(1.1);
}

.completion-progress {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  height: 20px;
  overflow: hidden;
  margin: 20px 0;
}

.completion-bar {
  height: 100%;
  background: linear-gradient(90deg, #667eea, #764ba2);
  transition: width 0.5s ease;
  border-radius: 20px;
}

/* Animations */
@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

@keyframes bounce {
  0%, 20%, 53%, 80%, 100% { transform: translate3d(0,0,0); }
  40%, 43% { transform: translate3d(0, -30px, 0); }
  70% { transform: translate3d(0, -15px, 0); }
  90% { transform: translate3d(0, -4px, 0); }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 40px, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fade-in-up {
  animation: fadeInUp 0.6s ease-out;
}

.bounce {
  animation: bounce 1s ease-in-out;
}

/* Responsive Design */
@media (max-width: 768px) {
  .letter-tile {
    width: 40px;
    height: 40px;
    font-size: 20px;
    margin: 2px;
  }
  
  .option-button {
    padding: 12px 16px;
    font-size: 14px;
    margin: 4px;
  }
  
  .word-display {
    padding: 16px;
    margin: 16px 0;
  }
  
  .hint-box {
    padding: 12px;
    margin: 12px 0;
  }
}

@media (max-width: 480px) {
  .letter-tile {
    width: 35px;
    height: 35px;
    font-size: 18px;
    margin: 1px;
  }
  
  .option-button {
    padding: 10px 12px;
    font-size: 13px;
    margin: 2px;
  }
}
